import React, { useState, useEffect } from 'react';
import articleContent from './article-content'
import ArticleList from '../components/ArticleList';
import NotFound from './NotFoundPage';
import Comments from '../components/CommentsList';
import UpvotesSection from '../components/Upvotes';
import CommentSection from '../components/CommentSection';


const ArticlePage = ({ match }) => {
    const name = match.params.name
    const article = articleContent.find(article => article.name === name);

    const [articleInfo, setArticleInfo] = useState({ upvotes: 0, comments: [] });

    useEffect(() => {
        const fetchData = async () => {
            const result = await fetch(`/api/article/${name}`);
            const body = await result.json();

            setArticleInfo(body);
        }
        fetchData();
    }, [name]);

    if (!article) return NotFound

    const otherArticles = articleContent.filter(article => article.name !== name);
    return (
        (
            <>  {/* //adding React Fragments */}
                <h1> {article.title}</h1>
                <UpvotesSection articlename={name} upvotes={articleInfo.upvotes} setArticleInfo={setArticleInfo} />
                {article.content.map((para, i) => (
                    <p key={i}>{para}</p>
                ))}
                <Comments comments={articleInfo.comments} />
                <CommentSection aritcleName={name} setArticleInfo={setArticleInfo} />
                <h2>Other Related Articles</h2>
                <ArticleList article={otherArticles} />
            </>
        )
    )
}


export default ArticlePage;