import React from 'react';
import { Link } from 'react-router-dom';

class NavBar extends React.Component {


    render() {
        const { navs } = this.props;
        return (
            <nav>
                <ul>
        {navs.map((nav,i)=><Link key={i} to={nav.link}  >{nav.name}</Link>)}
                </ul>
            </nav>
        )
    }
}

export default NavBar
